<template>
  <div class="page page-buy">
    <div class="top-wrapper">
      <div :class="{ split: !!orderInfo.isSplit }" class="merchant">
        <div class="left">
          <van-image
            :src="$root.getImageUrl(orderInfo.sellerAvatar)"
            class="avatar"
            round
          >
            <div slot="error" class="defaultAvatar size45"></div>
          </van-image>
          <div class="seller-info">
            <div class="name">{{ orderInfo.sellerNickname }}</div>
            <Level :level="orderInfo.sellerCreditLevel"/>
          </div>
        </div>
        <div class="right">
          <div class="label">可购买总额（V币）</div>
          <div class="amount">{{ orderInfo.validAmount }}</div>
        </div>
      </div>
    </div>
    <div class="seller-credit-indicator">
      <div class="title">卖方信用资讯</div>
      <div class="row">
        <div class="col">
          <div class="label">{{ orderInfo.sellerIndicator.range }}日成单数</div>
          <div class="value">
            <span class="bold">
              {{ orderInfo.sellerIndicator.totalSuccessfulOrder }}
            </span>
          </div>
        </div>
        <div class="col">
          <div class="label">{{ orderInfo.sellerIndicator.range }}日成单率</div>
          <div class="value">
            <span class="bold">
              {{ orderInfo.sellerIndicator.totalSuccessRate }}%
            </span>
          </div>
        </div>
      </div>
      <div class="row">
        <div class="col">
          <div class="label">
            {{ orderInfo.sellerIndicator.range }}日平均放币时间
          </div>
          <div class="value">
            &lt;
            <span class="bold">
              {{ orderInfo.sellerIndicator.sellerAvgTime }}
            </span>
            分钟
          </div>
        </div>
        <div class="col">
          <div class="label">
            {{ orderInfo.sellerIndicator.range }}日平均付款时间
          </div>
          <div class="value">
            &lt;
            <span class="bold">
              {{ orderInfo.sellerIndicator.buyerAvgTime }}
            </span>
            分钟
          </div>
        </div>
      </div>
      <div class="row">
        <div class="col">
          <div class="label">买单</div>
          <div class="value">
            <span class="bold">
              {{ orderInfo.sellerIndicator.buyerOrder }}
            </span>
            笔
          </div>
        </div>
        <div class="col">
          <div class="label">卖单</div>
          <div class="value">
            <span class="bold">
              {{ orderInfo.sellerIndicator.sellerOrder }}
            </span>
            笔
          </div>
        </div>
      </div>
    </div>
    <van-form class="gray-8" @submit="onSubmit">
      <van-field
        v-model="form.amount"
        :placeholder="`${orderInfo.minBuyEach}～${orderInfo.validAmount}/V币`"
        :readonly="!orderInfo.isSplit"
        autocomplete="new-password"
        label="购买数量："
        type="number"
      >
      </van-field>
      <van-cell class="payment" title="支付方式：">
        <van-radio-group v-model="form.paymentType">
          <van-radio
            v-for="(item, index) in sellerPayments"
            :key="index"
            :disabled="!$root.validPayments[item]"
            :name="item"
            class="btn"
          >
            <van-button slot="icon" class="btn" native-type="button">
              <span :class="`pay${item}`" class="item"></span>
              {{ item | paymentMethod("content") }}
            </van-button>
          </van-radio>
        </van-radio-group>
      </van-cell>
      <van-cell>
        <van-radio-group v-model="form.buyerPaymentTypeId">
          <template v-for="(item, index) in paymentChannel">
            <template v-if="form.paymentType === PAYMENT_METHOD.BANK">
              <van-cell
                :key="index"
                :title="bankIdMappingName[item.bankId]"
                :value="item.bankNo | safeBankNumber"
                class="paymentChannel"
                @click="form.buyerPaymentTypeId = item.id"
              >
                <template #right-icon>
                  <van-radio :name="item.id"/>
                </template>
              </van-cell>
            </template>
            <template v-else>
              <VirtualPayment :key="index" :type="form.paymentType | paymentMethod" v-bind="item"
                              @click="form.buyerPaymentTypeId = item.id">
                <van-radio :name="item.id" style="margin-left:15px"/>
              </VirtualPayment>
            </template>
          </template>
        </van-radio-group>
      </van-cell>
      <van-button class="primary block" native-type="submit">
        确定购买
      </van-button>
      <p class="tip">确认购买后如未自动跳转请到交易记录中查看我的买单</p>
    </van-form>
  </div>
</template>

<script>
import {PAYMENT_METHOD} from "@/vendor/enum";
import {buyerCreatesOrder, marketPendingOrderDetail} from "@/apis/market";
import Level from "@/components/Level.vue";
import VirtualPayment from "@/components/business/VirtualPayment.vue";
import { cryptoMerchantMixins } from '@/vendor/mixins';

export default {
  components: {Level, VirtualPayment},
  mixins: [cryptoMerchantMixins],
  data() {
    return {
      PAYMENT_METHOD,
      form: {
        amount: "",
        buyerPaymentTypeId: "",
        paymentType: "",
      },
      orderInfo: {
        sellerIndicator: {},
      },
    };
  },
  computed: {
    sellerPayments() {
      return this.$root.parsePayments(this.orderInfo.paymentType);
    },
    paymentChannel() {
      return (
        (
          Object.entries(this.$root.payments)
            .map(([k, v]) => v)
            .find((item) => item.type === this.form.paymentType) || {}
        ).list || []
      );
    },
    bankIdMappingName() {
      return this.$root.banks.reduce(
        (previous, current) => ({
          ...previous,
          [current.bankId]: current.bankName,
        }),
        {}
      );
    },
  },
  // watch: {
  //   "form.paymentType"(newVal) {
  //     if (newVal == PAYMENT_METHOD.ALI || newVal == PAYMENT_METHOD.WE_CHAT) {
  //       this.form.buyerPaymentTypeId = this.paymentChannel[0].id;
  //     }
  //   },
  // },
  created() {
    this.$root.loadPayments()
    this.loadOrderInfo(this.$route.params.id);
    this.$root.$emit("root:set-topbar", ["left-back", "customer-service"]);
  },
  methods: {
    onSubmit() {
      if (!this.form.amount) return this.$toast.fail("请输入购买数额");
      if (this.form.amount.toNumber() < this.orderInfo.minBuyEach.toNumber())
        return this.$toast.fail(
          `购买数额不得小于最低可购买额${this.orderInfo.minBuyEach}`
        );
      if (this.form.amount.toNumber() > this.orderInfo.validAmount.toNumber())
        return this.$toast.fail(
          `购买数额不得大于最高可购买额${this.orderInfo.minBuyEach}`
        );
      if (!this.form.paymentType) return this.$toast.fail("请选择支付方式");
      if (!this.form.buyerPaymentTypeId)
        return this.$toast.fail("请选择支付账号");
      buyerCreatesOrder(
        this.$root.$tls.processParams(
          {
            pendingOrderId: this.orderInfo.pendingOrderId.toString(),
            amount: this.form.amount.toNumber(),
            buyerPaymentTypeId: this.form.buyerPaymentTypeId.toString(),
            paymentType: this.form.paymentType.toString(),
          },
          ["float", "shake"]
        ),
        this.BuyerApiPath
      ).then(({ok, data, msg}) => {
        if (ok) {
          this.$router.replace(`/transaction_buy/${data.orderId}?m=${this.isCryptoMerchant ? 1 : 0}`);
        } else {
          this.$toast.fail(msg);
        }
      });
    },
    loadOrderInfo(pendingOrderId) {
      marketPendingOrderDetail({pendingOrderId}, this.BuyerApiPath).then(({ok, msg, data}) => {
        if (ok) {
          if (!data.isSplit) {
            this.form.amount = data.validAmount;
          }
          this.orderInfo = data;

          const bindData = () => {
            this.sellerPayments.forEach((v) => {
              if (this.$root.validPayments[v]) {
                this.form.paymentType = v;
              }
            });
            if (!this.form.paymentType) {
              this.warnning();
            }
          };
          if (this.$root.isBindAnyPayment) bindData();
          else this.$root.loadPayments().then(({ok}) => ok && bindData());
        } else {
          this.$toast.fail(msg);
        }
      });
    },
    warnning() {
      this.$dialog.confirm({
        message:
          "您还未绑定该支付方式的支付账号,请绑定对应支付账号后再选择该支付方式",
        confirmButtonText: "去绑定",
        showCancelButton: true,
        beforeClose: (action, done) => {
          if (action === "confirm") {
            this.$router.replace("/add_payment");
          }
          done();
        },
      });
    },
  },
};
</script>

