<template>
  <div class="level">
    <div
      v-for="v in 5"
      :key="v"
      :class="{
        [`g${largeLevel}${
          (v == 5 ? levelModel == 0 : levelModel >= v || levelModel == 0)
            ? 'l'
            : ''
        }`]: true,
      }"
      class="grade mini"
    />
  </div>
</template>

<script>
export default {
  name: "Level",
  props: {
    level: {
      type: Number,
      default: 1,
    },
  },
  computed: {
    levelModel() {
      return this.level % 5;
    },
    largeLevel() {
      if (this.levelModel >= 1) {
        return (this.level - this.levelModel) / 5 + 1;
      } else {
        return this.level / 5;
      }
    },
  },
};
</script>

<style lang="scss">
.level {
  display: flex;

  .grade {
    margin-right: 5px;
  }
}
</style>
