import { q } from "@/vendor/q";
import { crypptoMerchantURL } from "../vendor/q";

// 我的挂单
export const myPendingOrderList = (data) =>
  q({
    url: `/Api/Market/PendingOrder/myPendingOrderList`,
    method: "POST",
    data,
  });

// 交易中心订单列表
export const marketPendingOrderList = (data, sellerType) =>
  q({
    url: `/Api/Market/${crypptoMerchantURL(sellerType)}PendingOrder/marketPendingOrderList`,
    method: "POST",
    data,
  });

// 我的订单列表
export const myOrderList = (data, isBlock = false) =>
  q({ 
    url: !isBlock?`/Api/Market/Order/myOrderList`:`/Api/User/Business/getOrderList`,
    method: !isBlock?"GET":"POST",
    data,
  });

